import React, { useEffect } from 'react';
import CloseSVG from 'assets/images/close.svg';
import { useSelector } from 'react-redux';

function Menu() {
  const { settings } = useSelector((state) => {
    return state;
  });

  useEffect(() => {
    let menu = document.querySelector('.mobile-menu');
    let closeIcon = document.querySelector('.close-icon');

    menu.addEventListener('transitionend', onTransitionEnd, false);
    menu.addEventListener('click', handleClick, false);
    closeIcon.addEventListener('click', toggleClassMenu, false);

    function handleClick(event) {
      if (event.target === menu) {
        toggleClassMenu();
      }
    }
    function toggleClassMenu() {
      menu.classList.add('menu--animatable');
      if (!menu.classList.contains('menu--visible')) {
        menu.classList.add('menu--visible');
        document.body.style.overflow = 'hidden';
      } else {
        menu.classList.remove('menu--visible');
        document.body.style.overflow = 'initial';
      }
    }
    function onTransitionEnd() {
      menu.classList.remove('menu--animatable');
    }

    return () => {
      menu.removeEventListener('transitionend', onTransitionEnd, false);
      menu.removeEventListener('click', handleClick, false);
      closeIcon.removeEventListener('click', toggleClassMenu, false);
    };
  }, []);

  return (
    <div className="mobile-menu">
      <div className={`app-menu mobileSidebar${settings.data.mobileSidebar}`}>
        <img className="close-icon" src={CloseSVG} alt="بستن منو" />
        <div className="content">
          در حال پیاده‌سازی...
          <br />
          منوی موبایل
        </div>
      </div>
    </div>
  );
}

export default Menu;
