const initialState = {
  isVisible: true,
  data: {
    appLogoSize: 2,
    appNameColor: 1,
    appNameSize: 2,
    menuPosition: 1,
    buttonAnimation: 1,
    backgroundNoise: 2,
    menuGroupAnimation: 2,
    menuGroupType: 4,
    menuGroupPosition: 2,
    startButton: 2,
    showOtcScrollButton: 1,
    mobileMenuIcon: 2,
    mobileSidebar: 1,
    securityAnimation: 4,
    feeAnimation: 1,
    globalizationAnimation: 1,
  },
};

function SettingsReducer(state = initialState, action) {
  switch (action.type) {
    case 'settings/data':
      const newState = { ...state };
      newState.data[action.payload.key] = action.payload.value;

      return newState;

    case 'settings/show':
      return {
        ...state,
        isVisible: true,
      };

    case 'settings/hide':
      return {
        ...state,
        isVisible: false,
      };

    default:
      return state;
  }
}

export default SettingsReducer;
